<template>
  <div class="history-price-box">
    <h3 class="title">LATEST CHINA GUIDELINE PRICE CHANGE</h3>
    <p class="sub-title">Unit: CNY/Ton</p>
    <div class="export-quota">
      <template>
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            prop="Date"
            label="Date"
            align="center">
          </el-table-column>
          <el-table-column
            prop="Gasoline"
            label="Gasoline"
            align="center">
          </el-table-column>
          <el-table-column
            prop="Diesel"
            label="Diesel"
            align="center">
          </el-table-column>
        </el-table>
      </template>
    </div>
  </div>
</template>

<script>
  export default {
    name: "history-price",
    data(){
      return {
        tableData: [{
          Date: '28-Feb-18',
          Gasoline: '-190',
          Diesel: '-185',
        },{
          Date: '9-Feb-18',
          Gasoline: '-170',
          Diesel: '-160',
        }, {
          Date: '26-Jan-18',
          Gasoline: '+65',
          Diesel: '+60',
        }, {
          Date: '12-Jan-18',
          Gasoline: '+180',
          Diesel: '+175',
        }]
      }
    }
  }
</script>
<style scoped>
  .history-price-box{
    margin-bottom: 30px;
  }
  .title{
    font-size: 16px;
    color: #333333;
    font-family: Arial-BoldMT;
    word-break: break-all;
    /*margin-bottom: 20px;*/
  }
  .sub-title{
    font-family: ArialMT;
    font-size: 14px;
    color: #000000;
    letter-spacing: 0;
    line-height: 18px;
    margin-bottom: 19px;
    margin-top: 5px;
  }
</style>



