  <div class="cont-right-yybg clearfix tabYse" id="Crudeoil">
    <h6><a href="javascript:;">CRUDE OIL</a><i class="c-ico inf-yybg-ico-ss"></i></h6>
    <div class="crudeoil-wrap">
      <ul class="right-yybg-lis clearfix tabLi">
        <li :class="{'yybg-lis-d':index==0}" v-for="(item,index) in changePrice">
          <a href="javascript:;" v-if="index == 0">{{item.indexName}}</a>
          <a href="javascript:;" v-if="index == 1">{{item.indexName}}</a>
          <a href="javascript:;" v-if="index == 2">{{item.indexName}}</a>
        </li>
      </ul>
      <ul class="yybg-lis-sj ry-ssbj-ul" v-for="(item,index) in text" :style="{display:(index==0)?'block':'none'}">
        <li class="yybg-li-tj">
          <em>{{item.indexName}}</em>
          <span class="yybg-li-tr" v-if="item.riseFall>0">{{item.smeiValue}}</span>
          <span class="yybg-li-tg" v-if="item.riseFall<0">{{item.smeiValue}}</span>
          <span class="yybg-li-ts" v-if="item.riseFall==0">{{item.smeiValue}}</span>
        </li>
        <li class="yybg-li-tjj" v-if="item.riseFall<=0">
          <i class="c-ico" :class="(item.riseFall>0)?'inf-zjj':'inf-zjb'"></i>
          {{item.riseFall}}
          <span class="yybg-li-tt">{{item.indexDate}}</span>
        </li>
        <li class="yybg-li-tjr" v-if="item.riseFall>0">
          <i class="c-ico" :class="(item.riseFall>0)?'inf-zjj':'inf-zjb'"></i>
          {{item.riseFall}}
          <span class="yybg-li-tt">{{item.indexDate}}</span>
        </li>
      </ul>
      <div class="right-ssbj tabNone chum" style="display:block">
        <div id="tabEachOneId" class="tabEach"></div>
      </div>
      <div class="right-ssbj tabNone">
        <div id="tabEachTwoId" class="tabEach"></div>
      </div>
      <div class="right-ssbj tabNone">
        <div id="tabEachThreeId" class="tabEach"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import echarts from 'echarts'
  import {
    indexData,
    indexRrData,
    eachOne,
    exponent,
    exponentZs,
    powerPrice,
    oilData,
    imgData,
    queryCarousels,
    Kecharts
  } from '../../api/info';
    export default {
        name: "c-r-u-d-e-o-i-l",
        data(){

          return {
            searchWord:'',
            num:0,
            text: [],
            formInline: {
              oilName: '',
              oilPlace: '',
              oilArea: '',
              oilApi: '',
              time: '',
              oiloilSulphur: 0,
              oilAcidity: 0,
              dateStart: '',
              dateEnd: ''
            },
            eachData: {
              tooltip: {
                trigger: 'axis'
              },
              legend: {
                data: []
              },
              grid: {

                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
              },
              xAxis: {
                type: 'category',
                boundaryGap: false,
                data: []
              },
              yAxis: {
                type: 'value'
              },
              series: [{
                name: '111',
                type: 'line',
                stack: '',
                data: []
              }, {
                name: '222',
                type: 'line',
                stack: '',
                data: []
              }]
            },
            data: {},
            newData:[], //
            hotData:[], //
            xianhuo:[], //
            zhaobiao:[],//
            shihuo:[],//
            iol:[], //
            fenIol:[], //
            midIol:[], //
            yuanIol:[], //
            chengIol:[], //
            fenxi:[],
            //newData:[],   // 
            //hotData:[],   // 
            //oilDayData:[],   // 
            //oilSpotData:[],   //
            //tenderData:[],   // 
            //oilInfoData:[],    //
            shipNewData: [], //
            oilMarketData: [], //
            dealNumData: [], //
            priceExData: [], //
            // officialPrice: [], //
            officialTime: '', // 
            changePrice: [], // 
            exponentData: [], // 
            oilDataList:[],// 
            barnerList:[],
            headList1:[],
            headList2:[],
            headList3:[],
            leftList1:[],
            leftList2:[],
            leftList3:[],
            centerList:[],
            imgUrlList:[],
            exponentZsData:[],
            imgUrlHead1:'',
            imgUrlHead2:'',
            imgUrlHead3:'',
            imgUrlLeft1:'',
            imgUrlLeft2:'',
            imgUrlLeft3:'',
            imgUrlBanner:'',
            imgUrlCenter:'',
            imgUrlRight:'',
            title01:'',
            title02:'',
            title03:'',
            bannerTitle01:'',
            bannerTitle02:'',
            bannerTitle03:'',
            options:{
              backgroundColor: '#fff',
              tooltip: {
                trigger: 'axis',
                axisPointer: {
                  type: 'cross'
                },
                backgroundColor: 'rgba(245, 245, 245, 0.8)',
                borderWidth: 1,
                borderColor: '#ccc',
                padding: 10,
                textStyle: {
                  color: '#000'
                }
              },
              axisPointer: {
                link: {xAxisIndex: 'all'},
                label: {
                  backgroundColor: '#777'
                }
              },
              grid: [
                {
                  top:'10%',
                  left: '9%',
                  right: '6%',
                  height: '50%'
                },
                {
                  left: '10%',
                  right: '8%',
                  top: '75%',
                  height: '12%'
                }
              ],
              xAxis: [
                {
                  type: 'category',
                  data: [],
                  scale: true,
                  boundaryGap : false,
                  axisLine: {onZero: false},
                  splitLine: {show: false},
                  splitNumber: 20,
                  min: 'dataMin',
                  max: 'dataMax',
                  axisPointer: {
                    z: 100
                  }
                },
                {
                  type: 'category',
                  gridIndex: 1,
                  data: [],
                  scale: true,
                  boundaryGap : false,
                  axisLine: {onZero: false},
                  axisTick: {show: false},
                  splitLine: {show: false},
                  axisLabel: {show: false},
                  splitNumber: 20,
                  min: 'dataMin',
                  max: 'dataMax'
                }
              ],
              yAxis: [
                {
                  scale: true,
                  splitArea: {
                    show: true
                  }
                },
                {
                  scale: true,
                  gridIndex: 1,
                  splitNumber: 2,
                  axisLabel: {show: false},
                  axisLine: {show: false},
                  axisTick: {show: false},
                  splitLine: {show: false}
                }
              ],

              series: [
                {
                  name: 'Dow-Jones index',
                  type: 'candlestick',
                  data: []
                },
                {
                  name: 'Volume',
                  type: 'bar',
                  xAxisIndex: 1,
                  yAxisIndex: 1,
                  data: []
                }
              ]
            }
          }
        },
        mounted(){
          this.init(); // 
          this.tabFn(); // tab
        },
        methods:{
          init() {
            var This = this;
              // 
              indexRrData({}).then((res) => {
                var This = this;
                this.changePrice = res.datas;
                setTimeout(function() {
                  $('.tabLi li').each(function(i) {
                    $(this).on('click', function() {
                      var _index = $(this).index();
                      $(this).addClass('yybg-lis-d').siblings('li').removeClass('yybg-lis-d');
                      $('.tabNone').hide();
                      $('.tabNone').eq(i).show();
                      $(this).parents('.tabYse').find('.ry-ssbj-ul:eq(' + _index + ')').show().siblings('ul.ry-ssbj-ul').hide();

                    });
                  });
                }, 300)

                $('.tabNone,.tabEach').css('width', $('.chum').innerWidth())
                this.tabEachOne();
              })

            // 
            // powerPrice().then((res) => {
            // 	for(var i = 0; i < 6; i++) {
            // 		this.officialPrice.push(res.datas[i])
            // 	}
            // 	this.officialTime = res.datas[0].pricingDate;
            // })

          },
          tabFn() {
            $('.tabYse').each(function() {
              var This = this;
              $(this).find('.ulTab li').on('click', function(i) {
                $(this).parent().next().find('a:eq(' + $(this).index() + ')').show().siblings('a').hide();
                $(this).addClass('yybg-lis-d').siblings('li').removeClass('yybg-lis-d');
                $(This).find('.divUl').hide();
                $(This).find('.divUl').eq($(this).index()).show();

                $(This).find('.x-more a').eq($(this).index()).show().siblings('a').hide();

              })
            })
          },
          tabEachOne: function() {
            var This=this;
            var myChart1 = echarts.init(document.getElementById('tabEachOneId'));
            var myChart2 = echarts.init(document.getElementById('tabEachTwoId'));
            var myChart3 = echarts.init(document.getElementById('tabEachThreeId'));

            // 
            function eachZong(obj, obj2,min,max) {
              var option = $.extend(true, option, This.eachData);

              option.series = [{
                name: '',
                type: 'line',
                stack: '',
                data: []
              }]
              option.legend.data = [''];
              option.xAxis.data=[];
              option.series[0].data=[];

              for(var i = 0, j = obj.length - 1; i < obj.length; i++, j--) {

                var changeTime=obj[j].indexDate;
                var arrTime=changeTime.split('-');
                changeTime=arrTime[1]+'/'+arrTime[2];

                option.xAxis.data.push(changeTime);    //  x 
                option.series[0].data.push(obj[j].smeiValue);    // y 
              }
                // console.log(option.xAxis.data)
                // console.log(option.series[0].data)
              option.yAxis.min=min;
              option.yAxis.max=max;
              obj2.setOption(option);
            }
            //    
            indexRrData({}).then((res) => {
              this.text = [];
              for(var i = 0; i < res.datas.length; i++) {
                var obj = {};
                obj.indexName = res.datas[i].indexName;
                obj.smeiValue = res.datas[i].priceList[0].smeiValue;
                obj.riseFall = res.datas[i].priceList[0].riseFall;
                obj.indexDate = res.datas[i].priceList[0].indexDate;
                this.text.push(obj);
              }

              eachZong(res.datas[0].priceList, myChart1,res.datas[0].min,res.datas[0].max);
              eachZong(res.datas[1].priceList, myChart2,res.datas[1].min,res.datas[1].max);
              eachZong(res.datas[2].priceList, myChart3,res.datas[2].min,res.datas[2].max);
            })
          }
        }
    }
</script>

<style scoped>
  #Crudeoil{
    margin-bottom: 30px;
  }
  .cont-right-yybg{
    margin-top: 30px;
  }
  .cont-right-yybg h6{
    margin-bottom: 15px;
  }


  .divUl,
  .tabNone {
    display: none;
  }

  #echartOne {
    height: 200px;
  }

  .tabEach {
    height: 250px;
    width: 338px;
    position: relative;
    top: -30px;
    padding:0 10px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .green {
    color: green;
  }

  .red {
    color: red;
  }

  .mgb0 {
    margin-bottom: 0;
    margin-right: 20px !important;
  }

  .w150 {
    width: 150px!important;
  }

  .w100 {
    width: 100px!important;
  }

  .w300 {
    width: 300px!important;
  }

  .x-more a{display: none;}
  .left-news-pic1 img{width:199px; height:340px;}
  .left-news-pic1 em{position:absolute;top:15px;left:0;width:199px;text-align:center;padding:5px 0;background:#000;filter:alpha(opacity=80);  -moz-opacity:0.8;  -khtml-opacity: 0.8;  opacity: 0.8;color:#fff;font-size:16px;}
  .left-news-pic em{position:absolute;top:15px;left:0;width:199px;text-align:center;padding:5px 0;background:#000;filter:alpha(opacity=80);  -moz-opacity:0.8;  -khtml-opacity: 0.8;  opacity: 0.8;color:#fff;font-size:16px;}
  .inf-news li{float:left;}
  .inf-news li a{float:left;display:block;padding:0 15px;line-height:30px;color:#333;font-size:14px;}
  .inf-news li a:hover{background:#85add6;font-size:16px;color:#fff; }
  .inf-news .yybg-lis-d a{background:#85add6; font-size:16px;color:#fff;}
  .inf-news-c li{border-bottom:1px solid #eee;padding:14px 0 12px;height:70px;}
  .inf-news-c .lin-no{border:none;}
  .inf-news-c li h3{width:100%;font-size:14px;position:relative;margin-bottom:3px;}
  .inf-news-c li h3 a{color:#000;}
  .inf-news-c li h3 a:hover{color:#317ee8;}
  .inf-news-c li span{width:100%;color:#666;line-height:21px;display:block; text-indent:24px;}
  .inf-news-c li h3 em{position:absolute; right:2px;top:0;color:#666;}
  .inf-news-kx a:hover{color:#317ee8;}
  .inf-news-kx em{position:absolute;left:0;top:14px !important;color:#666;}
  .yybg-li-tr{ position:absolute;right:0;top:-2px;color:#f00;font-size:24px; font-weight:normal;}
  .yybg-li-tg{ position:absolute;right:0;top:-2px;color:#00cc00;font-size:24px; font-weight:normal;}
  .yybg-li-tjr{color:#f00;width:calc(100% - 15px);padding-left:15px;}


  .crudeoil-wrap{
    border: 1px solid #ECECEC;
  }
  .ry-ssbj-ul{
    padding: 0 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }


</style>



