<template>
<div class="news-details-dialog">
  <el-dialog
    title="News detail"
    :visible.sync="dialogVisible"
    width="870px">
    <h3 class="news-title">{{modalDatas.title}}</h3>
    <div class="news-content" ref="modalContent">

    </div>
  </el-dialog>
</div>
</template>
<script>
    export default {
        name: "sub-channel-news-details",
        props:{
            visible:{
                type:Boolean
            },
          detailsData:{
              type:Object
          }
        },
        watch:{
          visible(val){
              this.dialogVisible = val
          },
          dialogVisible(val){
              this.$emit('update:visible', val)
          },
          detailsData:{
            deep:true,
            handler(val){
              // console.log(val)
              this.modalDatas = val
              this.renderContent()
            }
          }
        },
        mounted(){
            // console.log(this.visible)

        },
        data(){
          return {
            dialogVisible:false,
            modalDatas:{}
          }
        },
      methods:{
        renderContent(){
          let timer = setInterval(()=>{
            if(!!this.$refs.modalContent){
              clearInterval(timer)
              this.$refs.modalContent.innerHTML = this.modalDatas.textHtml.replace(/<br>/g,'').replace(/pre>/g,'div style="text-indent:25px">')

            }
          },0)
        }
      }
    }
</script>

<style scoped>
  pre{
    white-space: normal;
  }
.news-title{
  text-align: center;
  font-family: Arial-BoldMT;
  font-size: 20px;
  color: #333333;
  margin-bottom: 20px;
}
.news-content{
  font-family: ArialMT;
  font-size: 16px;
  /*height: 360px;*/
  color: #333333;
  letter-spacing: 0;
  /*overflow: auto;*/
  /*line-height: 32px;*/
}
</style>



