<template>
<div class="properties-comparison">
  <h3>PROPERTIES COMPARISON</h3>
  <div class="compare-box">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0" class="comparison-ruleForm">
        <el-form-item label="" prop="material1" style="margin-bottom: 10px;">
          <el-select v-model="ruleForm.material1" placeholder="Please Choose" class="compare-select">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="material2" style="margin-bottom: 20px;">
          <el-select v-model="ruleForm.material2" placeholder="Please Choose" class="compare-select">
            <el-option
              v-for="item in options2"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <input type="button" class="compare-btn"  @click="submitForm('ruleForm')" value="Compare" />
        </el-form-item>
      </el-form>
  </div>
</div>
</template>

<script>
    export default {
        name: "properties-comparison",
        data(){
          return {
            options: [{
              value: '1',
              name: 'GB 0# General diesel fuels 2015'
            }, {
              value: '2',
              name: 'GB(V) 0# AutomobiIe dieseI fuels 2016'
            }, {
              value: '3',
              name: 'GB(VI) 0# AutomobiIe dieseI fuels 2016'
            }, {
              value: '4',
              name: 'Astralian diesel fuels'
            }, {
              value: '5',
              name: 'Australian Automotive Diesel  2001'
            }, {
              value: '6',
              name: 'Sinochem QZ ulsd (Astralia)'
            }, {
              value: '7',
              name: 'Philippine diesel fuels'
            }, {
              value: '8',
              name: 'Sinochem QZ 50ppm diesel fuels (Philippine)'
            }, {
              value: '9',
              name: 'USA MVNRLM Motor vehicle diesel fuel'
            }, {
              value: '10',
              name: 'Indonesia HSD'
            }, {
              value: '11',
              name: 'Motor Vehicle ULSD '
            }, {
              value: '12',
              name: 'India HSD'
            }],
            options2: '',
            ruleForm:{
              material1:'',
              material2:''
            },
            rules:{
              material1: [
                { required: true, message: ' ', trigger: 'change' }
              ],
              material2: [
                { required: true, message: ' ', trigger: 'change' }
              ],
            }
          }
        },
      mounted(){
          this.options2 = JSON.parse(JSON.stringify(this.options))

      },
      methods:{
        submitForm(formName){
          this.$refs[formName].validate((valid)=>{
              if(valid){
                  this.$router.push({path:'/properties',
                    query:{
                      material1:this.ruleForm.material1,
                      material2:this.ruleForm.material2
                    }})
              }else{
                console.log('error submit!!');
                return false;
              }
          })
        }
      }
    }
</script>

<style scoped>
  .properties-comparison{
    margin-bottom: 30px;
  }
.properties-comparison>h3{
  font-family: Arial-BoldMT;
  font-size: 16px;
  color: #333333;
  letter-spacing: 0;
  margin-bottom: 20px;
}
  .compare-box{
    width: 300px;
    height: 188px;
    background: #F5F5F5;
    padding: 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  .compare-btn{
    width: 258px;
    height: 36px;
    background: #FFFFFF;
    border: 1px solid #F36523;
    border-radius: 2px;
    margin: 0 auto;
    cursor: pointer;
    font-family: ArialMT;
    font-size: 14px;
    color: #F36523;
    letter-spacing: 0;
  }
  .compare-select{
    width: 100%;
  }
</style>



