<template>
<div class="ExportQuota">
  <h3 class="title">2018 CHINA FUEL EXPORT QUOTA</h3>
  <p class="sub-title">Unit: Million Tons</p>
  <div class="export-quota">
      <template>
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            prop="Enterprise"
            label="Enterprise"
            align="center"
            width="80">
          </el-table-column>
          <el-table-column
            prop="Gasoline"
            label="Gasoline"
            align="center"
            width="63">
          </el-table-column>
          <el-table-column
            prop="Diesel"
            label="Diesel"
            align="center"
            width="48">
          </el-table-column>
          <el-table-column
            prop="Kerosene"
            label="Kerosene"
            align="center"
            width="67">
          </el-table-column>
          <el-table-column
            prop="Total"
            label="Total"
            align="center"
            width="45">
          </el-table-column>
        </el-table>
      </template>
  </div>
</div>
</template>

<script>
    export default {
        name: "export-quota",
        data(){
            return {
              tableData: [{
                Enterprise: 'SINOPEC',
                Gasoline: '1.26',
                Diesel: '3.48',
                Kerosene: '2.00',
                Total: '6.74'
              }, {
                Enterprise: 'PETROCHINA',
                Gasoline: '3.56',
                Diesel: '1.85',
                Kerosene: '0.34',
                Total: '5.75'
              }, {
                Enterprise: 'SINOCHEM',
                Gasoline: '0.97',
                Diesel: '1.02',
                Kerosene: '0.01',
                Total: '2.00'
              }, {
                Enterprise: 'CNOOC',
                Gasoline: '0.76',
                Diesel: '0.64',
                Kerosene: '0.35',
                Total: '1.75'
              }, {
                Enterprise: 'Total',
                Gasoline: '6.55',
                Diesel: '6.99',
                Kerosene: '270',
                Total: '16.24'
              }]
            }
        }
    }
</script>
<style scoped>
  .ExportQuota{
    margin-bottom: 30px;
  }
  .title{
    font-size: 16px;
    color: #333333;
    font-family: Arial-BoldMT;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
.sub-title{
  font-family: ArialMT;
  font-size: 14px;
  color: #000000;
  letter-spacing: 0;
  line-height: 18px;
  margin-bottom: 19px;
  margin-top: 5px;
}
</style>



